<template>
  <div class="animate__animated animate__fadeIn">
      <a-table :pagination="false" :dataSource="state.dataSource" :columns="state.column" bordered rowKey="id">
          <template #action>
              <div style="display: flex">
                  <a-button style="margin-right: 5px">编辑</a-button>
                  <a-button>修改</a-button>
              </div>
          </template>
      </a-table>
  </div>
</template>

<script>
import { reactive } from 'vue';
import { apiGetAllIncomeType } from '@/api/api';
export default {
    setup(props) {
        console.log(props)
        const state = reactive({
            column: [
                {
                    title: '#',
                    key: 'index',
                    dataIndex: 'index',
                    width: 60,
                    customRender: ({text, record, index}) => {
                        return {
                            children: index+1
                        }
                    },
                    align: 'center'
                },
                {
                    title: '类型名称',
                    key: 'incomeType',
                    dataIndex: 'incomeType',
                    align: 'center'

                },
                {
                    title: '类型key',
                    key: 'incomeTypeKey',
                    dataIndex: 'incomeTypeKey',

                },
                {
                    title: '修改时间',
                    key: 'updateTime',
                    dataIndex: 'updateTime',
                    align: 'center',
                    width: 180
                },
                {
                    title: '备注',
                    key: 'remark',
                    dataIndex: 'remark',
                    align: 'left'
                
                },
                {
                    title: '操作',
                    key: 'action',
                    dataIndex: 'action',
                    slots: { customRender: 'action' },
                    width: 150,
                    align: 'center'

                },
            ],
            dataSource: [],

        });
        apiGetAllIncomeType().then(res => {
            state.dataSource = res || [];
        })
        return {
            state
        }
    }
}
</script>

